<template>
  <div>
    <div class="qr-container" id="qrContainer">
      <img class="qrcode" src="./img/purchase2.jpg" alt="" />
      <div class="name">🚗🚗</div>
    </div>
    <button @click="downloadImg(4)">下载</button>
  </div>
</template>
<script>
import html2canvas from 'html2canvas'
export default {
  name: 'photoShop',
  data() {
    return {}
  },
  methods: {
    downloadImg(times) {
      html2canvas(document.getElementById('qrContainer'), {
        useCORS: true,
        scale: times
      }).then((resolve) => {
        let imgUrl = resolve.toDataURL('image/png') //此时就得到了dom元素转成了base64的图片
        if (imgUrl !== '') {
          var aLink = document.createElement('a')
          aLink.download = '收款二维码'
          aLink.href = imgUrl
          aLink.click()
        }
      })
    }
  }
}
</script>
<style scoped>
.qr-container {
  width: 140px;
  height: 160px;
  padding: 20px;
  background: pink;
  border-radius: 10px;
}
.qrcode {
  display: block;
  margin: 10px auto;
  padding: 10px;
  background: white;
  border-radius: 10px;
  width: 100px;
  overflow: hidden;
}
.name {
  margin: 0 auto;
  text-align: center;
  color: white;
  font-size: 16px;
  height: 24px;
  line-height: 24px;
}
</style>